//
// -- All section tags
//


@base-section-left:           16.7%;
@base-section-left-2:         ceil((@base-section-left + 21)); 
@base-section-left-3:         round((@base-section-left + 38)); 
@base-section-left-4:         round((@base-section-left + 59)); 

.image_about-us section{
	display: inline-block;
	position: absolute;
	top: 43.5%;
	font-family: @ptsans-bold;
	color:@blue_light;
	font-size: 28px;
}

section.first{
	left: @base-section-left;
}

.image_about-us section.second{
	left: @base-section-left-2;
}

.image_about-us section.thirty{
	left: round((@base-section-left-3 + 0.3),1);
}

.image_about-us section.forty{
	left: @base-section-left-4;
}